<script>
import {
  cme_gridValueLayer,
  cme_cogtifLayer2,
// } from "CME2D/CME2DCore/index.js";
} from "E://0509/cme2d-v1.0-0510/cme2d-v1.0----new/src/CME2D/CME2DCore";


import { defineComponent } from "vue";
// import { getCofTifData } from "./cogtifWorld.js";
export default defineComponent({
  setup() {
    let layerdata = null;
    let mapRef = null;
    const layerRef = { tifLayer: null, textLayer: null };
    const CogTifInfo = {};
    /**
     * @description: /
     * @param {object} map - 
     * @param {string} url - url
     * @param {object} data - 
     * @return {*}
     */
    let addLayer = (map, url, data) => {
      mapRef = map;
      // CogTifInfo.value = res
      CogTifInfo.configUnit = data?.configUnit
      // let bandsNumber = data.bandsNumber || 1; //UV
      layerdata = data; //


      
      let sources = []; //
      if (data.normalize) {
          // 
          sources = [
            {
              url: url, //tif
            
            },
          ];

        } else {
          // 
          sources = [
            {
              url: url, //tif
              min: -9999,
               max: 9999 
            },
          ];
        }

      // 
      let config = {
        layerName: data.id,
        type: 'grid',
        zIndex: data.zIndex ?? 1,
        source: {
          //
          normalize: data.normalize ?? false, // true false
          // sources: [{ url, min: -9999, max: 9999 }], // tif

          sources:sources, // tif
          wrapX: data.wrapX ?? true,
          interpolate: data?.interpolate ?? true,  // 
        },
        style: {
          //
          color: data.legendData?.legend ?? [], //
          radio: +data.radio || 2, // 12
          scale: +(data.scale || data.scala) , // data.legendData?.scala ?? 0,//
          offset: +data.offset || 0,
          unit: data.unit ?? ''
        },
        opacity: data.opacity ?? 0.78, //
        sourceOptions: {
          allowFullFile: false,
        },
        mapproj: map.getView().getProjection().getCode()
      };
      // let layer = new cme_cogtifLayer(config); //
      let layer = new cme_cogtifLayer2(); //
      map.addLayer(layer);
      layerRef.tifLayer = layer;
      return layer._render(config).then((meta) => {
        if (data.showText) {
          addTextLayer(map, layer, data); //
        }
        return Promise.resolve(layerRef, meta); // 
      });
    };

    /**
     * @description: 
     * @param {*} map - 
     * @param {Object} layer - 
     * @param {Object} data - 
     * @return {*}
     */
    let addTextLayer = (map, layer, data) => {
      const range = { min: -9999, max: 9999 };
      const legend = (data?.legendData?.legend || []).map(v => v[0]);
      if (legend.length) {
        range.min = legend.reduce((v1, v2) => Math.min(v1, v2), 0) - 10;
        range.max = legend.reduce((v1, v2) => Math.max(v1, v2), 0) + 10;
      }
      let config = {
        map: map, //
        params: {
          type: 'grid',
          layer: layer, //
          layerName: data.textId ?? data.id, //,id_Text
          precision: 1, //
          opacity: 1, //
          maxZoom: 15, // 
          zIndex: Number(data.zIndex) + 1 ?? 2, //
          showTextRange: range,
          distanceArrow: data.distanceArrow ?? 80, //
          textStyle: data.textStyle ?? {
            //
            font: "11px Calibri,sans-serif", //
            fill: {
              color: "#000000", //
            },
            stroke: {
              // strokeStyle:'white',
              lineWidth: 5
            }
          },
        },
      };
      let lyr = new cme_gridValueLayer(); //
      map.addLayer(lyr); //
      lyr._render(config); //
      layerRef.tifLayer = lyr;
    };

    const getData = (pixel, layer) => {
      let res = layer.getData(pixel)
      let { scale = 1, unit, offset = 0, nodata, configUnit } = CogTifInfo
      let params = {
        value: null
      }
      if (res !== nodata) {
        try {
          if (configUnit && configUnit === 'k') {
            params.value = (res[0] / scale) + offset
          }
          else if (unit === 'K') {
            if (res[0] === 0) {
              params.value = 0
            }
            else {
              params.value = offset + (res[0] / scale) - 273.15
            }
          }
          else {
            params.value = (res[0] / scale) + offset
          }
        }
        catch (err) {
        }
        return params
      }
      return ''
    };

    function removeLayer() {
      Object.values(layerRef).forEach(layer => {
        if (!layer) return;
        mapRef && mapRef.removeLayer(layer);
      });
    }

    return {
      getData,
      addLayer,
      removeLayer
    };
  },
});
</script>
