import commonStyle from "../../style/commonStyle.js";

function PanByPixel(map, x, y) {
  // console.log("map.getView", map.getView);
  // console.log("map.getView()", map.getView());
  // if( !map.getView())
  //   return;

  const map_center = map.getView().getCenter(); //
  const centerPixel = map.getPixelFromCoordinate(map_center); //
  const newX = centerPixel[0] - x; //
  const newY = centerPixel[1] - y; //
  map.getView().animate({
    center: map.getCoordinateFromPixel([newX, newY]), //
    duration: 1000,
    zoom: map.getView().getZoom(), //
  }); //setCentersetZoom
}

function handlerColor(legend, scala, radio) {

  let color1 = ["case", ["!=", ["band", 2], 0]];
  let color = ["interpolate", ["linear"],
    ["band", 1]
  ];
  if (legend == null) {
    return null;
  }
  if (radio == 1) {


    for (let i = 0; i < legend.length; i++) {
      color = color.concat([legend[i][0] * scala, legend[i][1]]); // //console.log(legend[i]);
    }
    color1.push(color);
    color1.push(["color", 0, 0, 0, 0]);
    console.log("------------------");
    // console.log(color);
    console.log(JSON.stringify(color1));
    //  color = color.concat([legend[i][0] * scala, legend[i][1]]); // //console.log(legend[i]);
  } else {

    const resultArray = [
      "case",
      ["==", ["band", 2], 0],
      ["color", 0, 0, 0, 0], // 
      ["<=", ["band", 1], legend[0][0] * scala],
      ["color", ...legend[0][1]],
    ];
    // for (let i = 0; i < legend.length; i++) {
    for (let i = 0; i < legend.length; i++) {
      const [value, color] = legend[i];
      const [value1, color1] =
      i >= legend.length - 1 ? legend[i] : legend[i + 1];
      resultArray.push(["between", ["band", 1], value * scala, value1 * scala]);
      // resultArray.push(["color", ...color1]);
      resultArray.push(["color", ...color]);
    }
    resultArray[resultArray.length] = [
      "color",
      ...legend[legend.length - 1][1],
    ];
    color1 = resultArray;
  }
  console.log("JSON.stringify(color1)");
  console.log(JSON.stringify(color1) );
  return color1;
}

function addCommonStyle(style, styleCommon) {
  style = [];

  // let styleCollection = [];
  for (let key in styleCommon) {

    style[key] = commonStyle(styleCommon[key]);
  }
  return style;
}

