function getWindSpeedAndDirection(u, v) {
  let data = {}
  const speed = Math.sqrt(u * u + v * v)
  let dir = 270.0 - Math.atan2(v, u) * 180.0 / Math.PI
  if (dir >= 360) dir = dir - 360
  data.speed = speed;
  data.direction = dir.toString();
  return data;
};

